export default class BSCExplorer {
    async getWeb3() {
        if (window.ethereum) {
            const web3 = new Web3(window.ethereum);
            try {
                // ask user permission to access his accounts
                await window.ethereum.request({ method: "eth_requestAccounts" });
                return web3;
            } catch (error) {
                throw error;
            }
        } else {
            throw new Error("Must install Metamask");
        }
    }

    async getWeb3_TEST() {
        const web3 = new Web3(new Web3.providers.HttpProvider("http://localhost:8545"));
        return web3;
    }

    async getChainID() {
        return await this.web3.eth.getChainId();
    }

    async getAccount(idx) {
        const account = (await window.ethereum.request({ method: 'eth_accounts' }))[idx];
        return account;
    }

    setAccount(account) {
        this.account = account;
    }
    /*
    async getPAUContract() {
        const PAUTokenContract = new this.web3.eth.Contract(
            PAUTokenABI,
            PAU_TOKEN_CONTRACT,
            { from: await this.getAccount() }
        );
        return PAUTokenContract;
    }
*/
    async getPAUSaleContract() {
        const CriptoColhoesContract = new this.web3.eth.Contract(
            contractABI,
            CRIPTOCOLHOES_CONTRACT,
            { from: await this.getAccount() }
        );
        return CriptoColhoesContract;
    }

    async getBUSDContract() {
        const BUSDContract = new this.web3.eth.Contract(
            contractABIBUSD,
            "0xed24fc36d5ee211ea25a80239fb8c4cfd80f12ee",
            { from: await this.getAccount() }
        );
        return BUSDContract;
    }

    // eslint-disable-next-line no-useless-constructor
    constructor() {
        this.init = false;
    }

    async initialize() {
        try {
            this.web3 = await this.getWeb3();
            this.account = await this.getAccount(0);
            this.CriptoColhoesContract = await this.getPAUSaleContract();
            this.busd = await this.getBUSDContract();
            this.init = true;
            return this;
        }
        catch (error) {
            throw error;
        }
    }

    async reloadContracts() {
        this.PAUSaleContract = await this.getPAUSaleContract();
    }

    /* Sale Contract Methods
    async buyPreSale(qtd) {
        // qtd em BNB
        return await this.PAUSaleContract.methods.create(this.account).send({ from: this.account, value: Units.convert(qtd, "eth", "wei") });
    }*/

  /*  async getPAUBalance() {
        return Units.convert(
            await this.PAUTokenContract.methods
                .balanceOf(this.account)
                .call({ from: this.account }),
            "wei",
            "eth"
        );
    }

    async getBNBBalance() {
        return Units.convert(
            await this.web3.eth.getBalance(this.account),
            "wei",
            "eth"
        );
    }*/

    async getRate() {
        return await this.CriptoColhoesContract.methods.getRate().call({ from: this.account });
    }

    async getPrize(acc) {
        return await this.CriptoColhoesContract.methods.getPrize(acc).call({ from: this.account });
    }

    async getPintelho() {
        return await this.CriptoColhoesContract.methods.pintelho().call({ from: this.account });
    }

    async getAllowance(acc) {
        return await this.busd.methods.allowance( acc,  CRIPTOCOLHOES_CONTRACT).call({ from: this.account });
    }


    async buy(nLotto, amount) {
            return await this.CriptoColhoesContract.methods.batchBuyLottoTicket(nLotto, amount).send({from: this.account});

    }

    async claimReward() {
        return await this.CriptoColhoesContract.methods.claimReward().send({ from: this.account });
    }

    async getUserTickets(lotto, acc) {
        return await this.CriptoColhoesContract.methods.getUserTickets(lotto, acc).call({ from: this.account });
    }

    async getUserTickets2(lotto) {
        return await this.CriptoColhoesContract.methods.getUserTickets(lotto, this.account).call({ from: this.account });
    }

    async approve() {
        return await this.busd.methods.approve( CRIPTOCOLHOES_CONTRACT,  100000000000000000000000000000000000000000000000000n).send({ from: this.account });
    }

    async getLottery() {
        return await this.CriptoColhoesContract.methods.getLotteryID().call({ from: this.account });
    }

    async getBasicLottoInfo(lotto){
        return await this.CriptoColhoesContract.methods.getBasicLottoInfo(lotto).call({ from: this.account });
    }

    async getTicketOwner(winner){
        return await this.CriptoColhoesContract.methods.getOwnerOfTicket(winner).call({ from: this.account });
    }

    async getBusdBalance(add){
        return await this.busd.methods.balanceOf(add).call({ from: this.account });
    }

    async getBusdBalance2(){
        return await this.busd.methods.balanceOf(this.account).call({ from: this.account });
    }
}